/**
 * Create the chart when all data is loaded
 * @returns {undefined}
 */
function createChart() {

  var chart = Highcharts.stockChart('container', {

    xAxis: {
      crosshair: {
        width: 1,
        color: 'gray',
        dashStyle: 'shortdash',
        label: {
          backgroundColor: '#333333',
          enabled: true,
          style: {
            color: '#ffffff',
          },
        }
      }
    },

    yAxis: [{
      opposite: false,
      lineWidth: 1,
      crosshair: {
        width: 1.5,
        color: 'gray',
        dashStyle: 'shortdash',
        label: {
          enabled: true,
          format: '{value:.1f}',
        }
      },
      labels: {
        style: {
          color: orange,
        },
        align: 'right',
        format: '{value:.1f}',
        y: 6,
        x: -6
      }
    }, {
      opposite: true,
      lineWidth: 1,
      crosshair: {
        width: 1.5,
        color: 'gray',
        dashStyle: 'shortdash',
        label: {
          enabled: true,
          format: '{value:.1f}',
        }
      },
      labels: {
        style: {
          color: blue,
        },
        align: 'left',
        format: '{value:.1f}',
        y: 6,
        x: 10
      }
    }],

    tooltip: {
      backgroundColor: '#656d78',
      borderWidth: 0,
      style: {
        color: '#ffffff',
        fontSize: '10px',
      },
      formatter: function() {
        return '<span>Click for details</span>';
      }
    },
    series: seriesOptions
  });

}

$.each(names, function(i, name) {

  $.getJSON('https://www.highcharts.com/samples/data/jsonp.php?filename=' + name.toLowerCase() + '-c.json&callback=?', function(data) {

    seriesOptions[i] = {
      name: name,
      data: data,
      yAxis: i,
      color: i === 0 ? orange : blue,
    };

    // As we're loading the data asynchronously, we don't know what order it will arrive. So
    // we keep a counter and create the chart when all the data is loaded.
    seriesCounter += 1;

    if (seriesCounter === names.length) {
      createChart();
    }
  });
});




